@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca:wght@300;400&display=swap');

:root{
    --primary:#fff;
}

.btn{
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border:none;
    cursor:pointer;
}

.btn--primary{
    background-color: var(--primary);
    color:rgb(80, 80, 227);
    padding: 8px 20px;
    border-radius: 4px;
    border:3px solid rgb(80, 80, 227);
    transition:all 0.3s ease-out;
    font-family: 'Lexend Deca', sans-serif;
    height: 75px;
}

.btn--outline{
    background-color:rgb(80, 80, 227);
    color: #fff;
    padding: 8px 20px;
    border-radius: 4px;
    border:3px solid rgb(80, 80, 227);
    transition:all 0.3s ease-out;
    font-family: 'Lexend Deca', sans-serif;
    height: 75px;
}

.btn--medium{
    padding:8px 20px;
    font-size: 20px;
}

.btn--large{
    padding:12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background: #fff;
    color:#242424;
    border: 3px solid rgb(80, 80, 227);
    transition:all 0.3s ease-out;
}

.btn--primary:hover{
    background: rgb(230, 230, 244);
}
