.pricing{
    margin: 30px 50px 20px 50px;
}

.pricing .pricing-head{
    text-align: center;
    margin-bottom: 50px;
}
.pricing .pricing-head h2{
    color: rgb(29, 29, 30);
    font-size: 25px;
    padding: 0 370px;
}
.pricing .pricing-head h2 span{
    text-decoration: underline;
    text-decoration-color: rgb(251, 173, 48);
    text-decoration-thickness: 7px;
}

.pricing .service-boxes{
    display: flex;
    justify-content: space-evenly;
}

.pricing .service-boxes .box-1{
    width: 300px;
    height: 600px;
    border: 2px solid rgb(79, 90, 246);
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 20px;
    margin:30px 0 ;
}
.pricing .service-boxes .box-2{
    width: 300px;
    height: 600px;
    border: 4px solid rgb(79, 90, 246);
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 20px;
    background-color: rgb(79, 90, 246);
    margin:30px 0 ;
}
.pricing .service-boxes .box-3{
    width: 300px;
    height: 600px;
    border: 2px solid rgb(79, 90, 246);
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 20px;
    margin:30px 0 ;
}




.pricing .service-boxes .box-2 h3{
    color: #fff;
    font-size: 20px;
}
.pricing .service-boxes .box-2 h2{
    color: rgb(48, 48, 50);
    font-size: 16px;
}
.pricing .service-boxes .box-2 h2 span{
    color: #fff;
    font-size: 30px;
    font-weight: 900;
}
.pricing .service-boxes .box-2 h4{
    color: #fff;
    font-size: 19px;
    font-weight: 500;
}
.pricing .service-boxes .box-2 p{
    color: #fff;
    font-size: 15px;
}
.pricing .service-boxes .box-2 p i{
    padding: 0 5px;
    font-size: 20px;
}





.pricing .service-boxes h3{
    color: rgb(79, 90, 246);
    font-size: 20px;
}
.pricing .service-boxes h2{
    color: rgb(118, 118, 121);
    font-size: 16px;
}
.pricing .service-boxes h2 span{
    color: rgb(68, 68, 239);
    font-size: 30px;
    font-weight: 900;
}
.pricing .service-boxes h4{
    color: rgb(79, 90, 246);
    font-size: 19px;
    font-weight: 500;
}
.pricing .service-boxes p{
    color: rgb(79, 90, 246);
    font-size: 15px;
}
.pricing .service-boxes p i{
    padding: 0 5px;
    font-size: 20px;
}

.pricing .service-boxes .btn-continue{
    width: 300px;
    height: 45px;
    color: #fff;
    font-size: 20px;
    background-color:  rgb(79, 90, 246);
    border-radius: 10px;
    font-weight: bold;
}

.pricing .service-boxes .btn-continue:hover{
    color: rgb(79, 90, 246);
    background-color:#fff;
    transition: ease-in 0.3s;
}

@media (max-width:1200px) {
    .pricing .pricing-head h2{
        font-size: 25px;
    padding: 0 200px;
    }
}
@media (max-width:1100px) {
    .pricing .pricing-head h2{
        font-size: 25px;
    padding: 0 100px;
    }
}

@media (max-width:960px) {
    .pricing .service-boxes{
        flex-wrap: wrap;
    }
    .pricing .pricing-head h2{
        font-size: 23px;
    padding: 0 20px;
    }
}

.eclipse10{
    position: relative;
    bottom: 950px;
    left: 200px;
}
.eclipse11{
    position: relative;
    bottom: 1000px;
    left: 1000px;
}