@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca:wght@300;400&display=swap');

.navbar {
    background: #fff;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    font-weight: 500;
    position: sticky;
    top: 0;
    z-index: 999;
    font-family: 'Lexend Deca', sans-serif;
    margin: 20px 30px 20px 10px;
  }

  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 120px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    margin-top: 5px;
    margin-left: 15px;
    cursor: pointer;
    display: flex;
    align-items: center;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links-active {
    color: rgb(61, 60, 60);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    font-size: 21px;
  }
  .nav-links {
    color: rgb(115, 113, 113);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 0.7rem;
    height: 100%;
    font-size: 21px;
  }
  
  .nav-links:hover {
    color:rgb(31, 29, 29);
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: rgb(32, 31, 31);
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
    margin-top: 10px;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 80vh;
      position: absolute;
      top: 80px;
      left: -130%;
      opacity: 1;
      transition: all 0.5s ease;
    }
    .nav-menu.active {
        background: #dfd73a;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
      }
      .nav-links-active {
        padding: 20px;
        padding-left: 40vw;
        width: 100%;
        height: 30px;
      }
      .nav-links {
        padding: 20px;
        padding-left: 40vw;
        width: 100%;
        height: 30px;
      }
      .nav-links-active:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 5px;
        height: 30px;
      }
      .nav-links:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 5px;
        height: 30px;
      }
    
      .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
      }
    
      .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
      }
    
      .fa-times {
        color: rgb(32, 31, 31);
        font-size: 2rem;
      }
    
      .nav-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: rgb(80, 80, 227);
        color: #fff;
        padding: 14px 20px;
        border: 2px solid #fff;
        transition: all 0.3s ease-out;
      }
    
      .nav-links-mobile:hover {
        background: #fff;
        color: #242424;
        transition: 250ms;
        border: 2px solid rgb(80, 80, 227);
      }
    }