@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca:wght@300;400&family=Noto+Sans:wght@900&display=swap');

.main-container{
    margin: -120px 50px 20px 50px;
}

.hero{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* flex-wrap: wrap; */
}

.hero .left p{
    color:rgb(87, 96, 227);
    font-size: 27px;
}

.hero .left h1{
    color:rgb(31, 30, 30);
    font-size: 42px;
    margin-top: -15px;
    font-family: 'Lexend Deca', sans-serif;

    font-family: 'Noto Sans', sans-serif;
}

.hero .left h1 span{
    text-decoration: underline;
    text-decoration-color: rgb(251, 173, 48);
    text-decoration-thickness: 7px;
}

.hero .right img{
    width:120%;
    margin-right: -380px;
}

.hero-btns{
    display: flex;
    flex-wrap: nowrap;
}

@media (max-width:960px) {
    .hero{
        
        flex-wrap: wrap;
    }
    .hero .right img{
        width:125%;
        margin-left: -50px;
    }
    .hero .left p{
        font-size: 25px;
    }
    .hero .left h1{
        font-size: 40px;
    }
}

@media (max-width:550px){
    .hero .left p{
        font-size: 20px;
    }
    .hero .left h1{
        font-size: 25px;
    }
}

.left-down{
    margin-top: -295px;
}

.left-down p{
    color:rgb(87, 96, 227);
    font-size: 27px;
}

.left-down h3{
    color:rgb(139, 139, 144);
    font-size: 24px;
}

@media (max-width:1300px){
    .left-down{
        margin-top: -200px;
    }
}
@media (max-width:1200px){
    .left-down{
        margin-top: -170px;
    }
}
@media (max-width:1100px){
    .left-down{
        margin-top: -150px;
    }
}
@media (max-width:1050px){
    .left-down{
        margin-top: -130px;
    }
}
@media (max-width:1020px){
    .left-down{
        margin-top: -100px;
    }
}
@media (max-width:1010px){
    .left-down{
        margin-top: -90px;
    }
}

@media (max-width:960px) {
    .left-down{
        margin-top: 0px;
    }
    .left-down p{
        font-size: 25px;
    }
    .left-down h3{
        font-size: 21px;
    }
}
@media (max-width:550px) {
    .left-down p{
        font-size: 21px;
    }
    .left-down h3{
        font-size: 18px;
    }
}

@media (max-width:960px) {
    .btn--primary{
        border:2px solid rgb(80, 80, 227);
       height: 65px;
    }
    
    .btn--outline{
        border:2px solid rgb(80, 80, 227);
        height: 65px;
    }
}

@media (max-width:580px) {
    .btn--primary{
        padding: 4px 10px;
       height: 60px;
       font-size: 16px;
    }
    
    .btn--outline{
        padding: 4px 10px;
        height: 60px;
        font-size: 16px;
    }
}