.services{
    margin: 30px 50px 0px 50px;
}

.services .services-text{
    padding-right: 660px;
    padding-left: 125px;
    font-size: 20px;
    margin-bottom: 100px;
}

.services .services-text p{
    color: rgb(45, 44, 44);
}

.services .services-text .link-claim{
    text-decoration: none;
}

.services .services-text .link-claim p{
   color: rgb(87, 96, 227);
}

.services .boxes-1-2{
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 170px;
}

.services .boxes-1-2 .box-1{
    height: 600px;
    width: 350px;
    border: 1px solid rgb(168, 166, 166);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    padding:30px 40px;
}
.services .boxes-1-2 .box-2{
    height: 600px;
    width: 350px;
    border: 1px solid rgb(168, 166, 166);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    padding:30px 40px;
    margin-top: -220px;
}

.services .boxes-1-2 img{
    padding: 0 50px;
 }

.services .boxes-1-2 h3{
    color: rgb(14, 14, 15);
    font-size: 24px;
}
.services .boxes-1-2 p{
    color: rgb(120, 120, 122);
    font-size: 20px;
    padding-right: 25px;
}
.services .boxes-1-2 .link-learn{
    text-decoration: none;
    display: flex;
}
.services .boxes-1-2 .link-learn p{
    color: rgb(87, 96, 227);
    font-size: 22px;
}
.services .boxes-1-2 .link-learn p:hover{
    color: rgb(128, 136, 250);
}
.services .boxes-1-2 .link-learn i{
    color: rgb(87, 96, 227);
    font-size: 25px;
    padding: 25px 25px 0 0;
}
.services .boxes-1-2 .link-learn i:hover{
    color: rgb(128, 136, 250);
}

.services .boxes-3-4{
    display: flex;
    justify-content: space-evenly;
}

.services .boxes-3-4 .box-3{
    height: 600px;
    width: 350px;
    border: 1px solid rgb(168, 166, 166);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    padding:30px 40px;
}
.services .boxes-3-4 .box-4{
    height: 600px;
    width: 350px;
    border: 1px solid rgb(168, 166, 166);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    padding:30px 40px;
    margin-top: -220px;
}

.services .boxes-3-4 img{
   padding: 0 45px;
}

.services .boxes-3-4 h3{
    color: rgb(14, 14, 15);
    font-size: 24px;
}
.services .boxes-3-4 p{
    color: rgb(120, 120, 122);
    font-size: 20px;
    padding-right: 25px;
}
.services .boxes-3-4 .link-learn{
    text-decoration: none;
    display: flex;
}
.services .boxes-3-4 .link-learn p{
    color: rgb(87, 96, 227);
    font-size: 22px;
}
.services .boxes-3-4 .link-learn p:hover{
    color: rgb(128, 136, 250);
}
.services .boxes-3-4 .link-learn i{
    color: rgb(87, 96, 227);
    font-size: 25px;
    padding: 25px 25px 0 0;
}
.services .boxes-3-4 .link-learn i:hover{
    color: rgb(128, 136, 250);
}

.services .services-text-bottom{
    padding-left: 680px;
    padding-right: 130px;
    font-size: 20px;
    margin-bottom: 10px;
    margin-top: -170px;
}

.services .services-text-bottom p{
    color: rgb(45, 44, 44);
}

.services .services-text-bottom .link-more{
    text-decoration: none;
    display: flex;
}

.services .services-text-bottom .link-more p{
   color: rgb(87, 96, 227);
}

.services .services-text-bottom .link-more p:hover{
    color: rgb(128, 136, 250);
}
.services .services-text-bottom .link-more i{
    color: rgb(87, 96, 227);
    font-size: 25px;
    padding: 20px 0 0 15px;
}

.services .services-text-bottom .link-more i:hover{
    color: rgb(128, 136, 250);
}

@media (max-width:1200px) {
    .services .services-text{
        padding-right: 500px;
        padding-left: 100px;
        font-size: 19px;
    }
    .services .services-text-bottom{
        padding-left: 550px;
        padding-right: 90px;
        font-size: 19px;
    }
}
@media (max-width:1100px) {
    .services .services-text{
        padding-right: 450px;
        padding-left: 80px;
        font-size: 19px;
    }
    .services .services-text-bottom{
        padding-left: 500px;
        padding-right: 60px;
        font-size: 19px;
    }
}
@media (max-width:960px) {
    .services .services-text{
        padding-right: 20px;
        padding-left: 20px;
        font-size: 18px;
        text-align: center;
    }
    .services .boxes-1-2{
        flex-wrap: wrap;
    }
    .services .boxes-3-4{
        flex-wrap: wrap;
    }
    .services .boxes-1-2{
         margin-bottom: 120px;
    }
    .services .boxes-1-2 .box-2{
         margin-top: 120px;
    }
    .services .boxes-3-4 .box-4{
         margin-top: 120px;
    }
    .services .services-text-bottom{
        padding-left: 20px;
        padding-right: 20px;
        font-size: 18px;
        text-align: center;
        margin-bottom: 10px;
    margin-top: 100px;
    }
    .services .services-text-bottom .link-more{
        justify-content: center;
    }
}


