.aboutcompany{
    margin: 30px 50px 20px 50px;
}
.aboutcompany .about-text{
    text-align: center;
}

.aboutcompany .about-text .about-text-head{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 50px;
}

.aboutcompany .about-text .about-text-head p{
    font-size: 20px;
    color: rgb(80, 80, 227);
}
.aboutcompany .about-text .about-text-head .video-link{
    font-size: 38px;
    color: rgb(80, 80, 227);
    padding: 8px 0 0 13px;
}
.aboutcompany .about-text .about-text-head .video-link:hover{
    color: rgb(70, 134, 244);
}

.aboutcompany .about-text .about-text-down h2{
   color: rgb(31, 30, 30);
    font-size: 30px;
}

.aboutcompany .about-text .about-text-down h2 span{
    text-decoration: underline;
    text-decoration-color: rgb(251, 173, 48);
    text-decoration-thickness: 6px;
}

.aboutcompany .about-text .about-text-down p{
    font-size: 23px;
    color: rgb(139, 139, 144);
    padding: 0 290px;
}

.aboutcompany .about-box{
    display: flex;
    text-align:left;
    margin-top: 50px;
}


.aboutcompany .about-box i{
    color: rgb(87, 96, 227);
    font-size: 50px;
    padding: 50px 6px 3px 10px;
}
.aboutcompany .about-box h3{
    color: rgb(31, 30, 30);
    font-size: 24px;
    padding: 5px 6px 3px 6px;
}

.aboutcompany .about-box p{
    color: rgb(125, 125, 129);
    font-size: 18px;
    padding: 5px 20px 3px 6px;
}

.aboutcompany .about-box .link-get-started{
    text-decoration: none;
}
.aboutcompany .about-box .link-get-started p{
    color:rgb(251, 173, 48);
    font-size: 21px;
    padding: 5px 6px 3px 6px;
}
.aboutcompany .about-box .link-get-started p:hover{
    color:rgb(248, 205, 49);
}

@media (max-width:1160px) {
    .aboutcompany .about-text .about-text-down p{
        padding: 0 100px ;
    }
}
@media (max-width:960px) {
    .aboutcompany .about-text .about-text-head p{
        font-size: 18px;
    }
    .aboutcompany .about-text .about-text-down h2{
        font-size: 25px;
    }
    .aboutcompany .about-text .about-text-down p{
        font-size: 20px;
        padding: 0 ;
    }
    .aboutcompany .about-box{
        display: flex;
        flex-wrap: wrap;
        text-align: center;
    }
}
@media (max-width:560px) {
    .aboutcompany{
        margin: 50px 50px 20px 50px;
    }
    .aboutcompany .about-text .about-text-head p{
        font-size: 17px;
    }
}

