.footer{
    margin: 20px 0px 20px 0px;
}

.footer .footer-container{
    background-color: rgb(209, 210, 203);
    height: 20vh;
    border: 2px solid rgb(79, 90, 246);
    display: flex;
    justify-content:center;
}
.footer .footer-bottom{
    text-align: center;
}

.footer .footer-container .links .social-links{
    padding-top: 5vh;
}
.footer .footer-container .links .social-links i{
    font-size: 40px;
    color:rgb(52, 53, 54);
    padding: 10px 10px;
}
.footer .footer-container .links .social-links i:hover{
    color:rgb(16, 31, 236);
}
