@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca:wght@300;400&display=swap');

body{
    background: #fff;
}

.container{
    margin: 60px 50px 20px 50px;
}

.container .search-container{
    display: flex;
    justify-content: flex-end;
}


.search {
  width: 33%;
  position: sticky;
  display: flex;
}

@media screen and (max-width: 960px) {
    .search {
        width: 100%;
      }
      
}

.searchTerm {
  width: 80%;
  border: 2px solid rgb(80, 80, 227);
  border-left: none;
  padding: 5px;
  height: 44px;
  border-radius: 0px 5px 5px 0px;
  outline: none;
  color: rgb(87, 96, 227);
  font-size: 20px;
  font-family: 'Lexend Deca', sans-serif;
}

.searchTerm:focus{
  color: rgb(61, 61, 192);
}

@media screen and (max-width: 960px) {
      .searchTerm{
        width: 100%;
      }
}

.searchButton {
  width: 65px;
  height: 58px;
  border: 2px solid rgb(87, 96, 227);
  background: #fff;
  text-align: center;
  color: rgb(87, 96, 227);
  border-right: none;
  border-radius: 5px 0px 0px 5px;
  cursor: pointer;
  font-size: 27px;
}

.eclipse1{
    position: relative;
    left: 90px;
    bottom: 100px;
    z-index: -100;
}
.eclipse2{
    position: relative;
    left: 335px;
    bottom: 20px;
    z-index: -100;
}
.eclipse3{
    position: relative;
    left: 910px;
    bottom: 75px;
    z-index: -100;
}
.eclipse4{
    position: relative;
    left: 510px;
    bottom: 245px;
    z-index: -100;
}

@media (max-width:960px) {
  .eclipse3{
    position: relative;
    left: 290px;
    top: 550px;
}
}
